* {
  font-family: sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-size: large;
  color: white;
}

html {
  font-size: 10px;
  // background-color: #1A1A1A;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}


#app {
  display: flex;
  justify-content: space-around;
}

h3 {
  color: #333;
}


#grable-human, #grable-computer {
    background-color: #333;
    border: 1px solid gold;
}
#grable-human td, #grable-computer td {
    width: 20px;
    height: 20px;
    border: 1px solid silver;
    text-align: center;
}

#grable-computer .missed-shot {
  background-color: white;
}

table {
  margin: 10px;
}

.disable {
  pointer-events: none;
}